@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
.square {
  background: #fff;
  border: 1px solid #999;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 100px;
  margin-right: -1px;
  margin-top: -1px;
  text-align: center;
  width: 100%;
  padding: 0px;
  cursor: pointer;
}

.square:focus {
  outline: none;
}

.board-grid {
    display: grid;
    grid-gap: 10px;
    grid-template-columns: repeat(4, minmax(50px, 1fr));
    grid-auto-rows: minmax(50px, auto);
}
.menu-container {
  margin: 15px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
}

.menu-option {
  margin: 10px;
  padding: 10px;
  color: white;
  font-size: 13px;
  width: 100px;
  background-color: brown;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.16), 0 2px 4px rgba(0, 0, 0, 0.26);
  transition: box-shadow 0.2s cubic-bezier(0.895, 0.03, 0.685, 0.22);
  text-transform: uppercase;
  cursor: pointer;
}

.menu-option:hover {
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.16), 0 6px 12px rgba(0, 0, 0, 0.32);
  transition: box-shadow 0.2s cubic-bezier(0.165, 0.84, 0.44, 1);
  background-color: #d30000;
  color: white;
}

.menu-option:focus {
  outline: 0;
}

.menu-option:disabled {
  background-color: rgb(95, 95, 95);
}

.menu-option:disabled:hover {
  background-color: rgb(95, 95, 95);
  cursor: not-allowed;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.16), 0 2px 4px rgba(0, 0, 0, 0.26);
  transition: box-shadow 0.2s cubic-bezier(0.895, 0.03, 0.685, 0.22);
}

body {
  background-color: #e4e6eb;
}

.page-content {
  text-align: center;
  margin: 50px 15%;
}

.time-machine-messages {
  margin-top: 20px;
  margin-bottom: 20px;
}

.container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.board {
  flex-grow: 1;
}

@media only screen and (max-width: 900px) {
  .page-content {
    text-align: center;
    margin: 50px 5%;
  }
}

.tic-tac-toe-square {
  padding: 0rem 0rem;
  margin: 0 0rem;
  font-size: 8vw;
  font-family: 'Cabin Sketch', cursive;
  text-align: center;
  display: flex;
  justify-content: center;
  background-color: white;

  border-top-left-radius: 255px 15px;
  border-top-right-radius: 15px 225px;
  border-bottom-right-radius: 225px 15px;
  border-bottom-left-radius: 15px 255px;
}

.tic-tac-toe-square:before {
  content: '';
  display: block;
  height: 0;
  width: 0;
  padding-bottom: calc(11 / 16 * 80%);
}

.tic-tac-toe-square:hover {
  box-shadow: 2px 8px 4px -6px hsla(0, 0%, 0%, 0.3);
}

.tic-tac-toe-square.dashed.thick {
  border: dashed 3px #41403e;
}

@media only screen and (max-width: 900px) {
  .tic-tac-toe-square {
    font-size: 20vw;
  }
}

.tic-tac-toe-board-grid {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(3, minmax(50px, 1fr));
  grid-template-rows: 1fr;
}

.ttt-menu-button {
  background: transparent;
  padding: 0.5rem 0.5rem;
  margin: 0 0.5rem;
  font-size: 1rem;
  background-color: brown;
  font-family: 'Architects Daughter', cursive;
  color: white;

  border-top-left-radius: 255px 15px;
  border-top-right-radius: 15px 225px;
  border-bottom-right-radius: 225px 15px;
  border-bottom-left-radius: 15px 255px;
}

.ttt-menu-button:disabled {
  background-color: rgb(95, 95, 95);
}

.ttt-menu-button:disabled:hover {
  background-color: rgb(95, 95, 95);
  cursor: not-allowed;
}

.ttt-menu-button:hover {
  box-shadow: 2px 8px 4px -6px hsla(0, 0%, 0%, 0.3);
  background-color: #d30000;
}

.ttt-menu-button.lined.thick {
  border: solid 3px #41403e;
}

.next-to-move-header {
  font-family: 'Architects Daughter', cursive;
}

.next-to-move {
  font-family: 'Cabin Sketch', cursive;
  font-size: 150px;
  top: -40px;
  position: relative;
}

.tic-tac-toe-title {
  font-family: 'Architects Daughter', cursive;
}

.tic-tac-toe-messages {
  margin-top: 20px;
  margin-bottom: 20px;
  font-family: 'Architects Daughter', cursive;
}

.tic-tac-toe-messages > h5 {
  margin-bottom: 10px;
}

.tic-tac-toe-is-traveling {
  color: grey;
}

.tic-tac-toe-winner {
  font-size: x-large;
  color: green;
}

.tic-tac-toe-error {
  color: red;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:focus {
  outline: none;
}

button {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
  background-color: transparent;
  border: transparent;
  font-size: 16px;
}

nav {
  display: flex;
  flex-wrap: wrap;
  background-color: teal;
  padding: 0 10% 10px;
  justify-content: space-between;
  box-shadow: 0 0 10px #00000077;
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
}

.logo {
  font-family: 'Pacifico', cursive;
  font-size: 2rem;
  transition: color 0.3s ease;
}

.logo:hover,
.logo:focus {
  color: #111;
}

ul {
  list-style: none;
  display: flex;
  align-items: center;
}

li {
  margin-left: 2em;
}

.link {
  position: relative;
  text-decoration: none;
  color: black;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
}

.link::after {
  content: '';
  display: block;
  position: absolute;
  background-color: #444;
  width: 100%;
  height: 0.2em;
  bottom: -0.3em;
  left: 0;
  border-radius: 0.1em;
  transform: scaleX(0);
  transition: transform 0.3s ease;
  transform-origin: right;
}

.link:hover::after,
.link:focus::after {
  transform: scaleX(1);
  transform-origin: left;
}

.menu-label,
.menu-button {
  display: none;
}

.username {
  font-size: 16px;
}

@media screen and (max-width: 600px) {
  .menu-label {
    display: inline;
    align-self: center;
    cursor: pointer;
  }

  ul {
    margin: 0.5em 0;
    display: none;
    flex: 100% 1;
  }

  li {
    margin-left: 0;
    text-align: center;
    margin-bottom: 1em;
  }

  #toggle:checked + ul {
    display: block;
  }
}

body {
  font-family: 'Roboto', sans-serif;
}

