.square {
  background: #fff;
  border: 1px solid #999;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 100px;
  margin-right: -1px;
  margin-top: -1px;
  text-align: center;
  width: 100%;
  padding: 0px;
  cursor: pointer;
}

.square:focus {
  outline: none;
}
